<template>
	<div class="article-container">
		<div class="article-header">
			<div class="article-zhong">
				<div class="article-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="article-ti">文章详情</div>
				<div></div>
			</div>
		</div>
		<div class="article-content">
			<div>
				<div class="article-shang">{{data.article_title}}</div>
				<div class="article-flex">
					<div class="article-shi">
						<img src="../assets/icon_shijian.png">
					</div>
					<div class="article-jian">{{$root.formatDate(data.create_time)}}</div>
				</div>
				<div v-html="data.article_content" class="article-zizhong">
					<!--{{data.article_content}}-->
				</div>
				<div class="article-tu" @click="openSimpleDialog(data)">
					<!--<img src="../assets/img_lazn.png">-->
					<img :src="!!data.article_pic?baseUrl+data.article_pic:'../assets/img_lazn.png'">
				</div>
				<!--<div class="article-xia">
					华为多年来一直在探索大数据的成功之道，历经了500+ 的大数据项目实践，在帮助客户成功的同时， 总结、提炼出了以“谋、聚、管、算、用”为支撑的五阶段大数据服务解决方案，覆盖企业大数据升级转型从运筹帷幄到决胜千里的端到端， 让企业客户的大数据“看得见、摸得到”，助力企业客户大数据“用得好，做得实”。
				</div>-->
			</div>
		</div>
		<mu-dialog width="100%" :open.sync="openSimple">
			<div class="article-lun" @click="openBotttomSheet">
		  		<mu-carousel interval='80000'>
					<mu-carousel-item>
						<img :src="baseUrl+data.article_pic">
					</mu-carousel-item>
				</mu-carousel>
		  	</div>
		  	<!--<div class="article-tantu" @click="openBotttomSheet">
		  		<img :src="!!img_url?baseUrl+img_url:'../assets/img_lazn.png'">
		  	</div>-->
	    </mu-dialog>
	    <mu-bottom-sheet :open.sync="open">
	   		 <mu-list @item-click="closeBottomSheet">
	      		<mu-sub-header>
	      			<div class="article-between">
	      				<div class="article-flex" @click="save()">
		      				<div class="article-xiazai">
		      					<img src="../assets/xiazai.png">
		      				</div>
		      				<div class="article-baocun">保存</div>
		      			</div>
		      			<div class="article-qux" @click=" closeSimpleDialog()">取消</div>
	      			</div>
	      		</mu-sub-header>
	    	</mu-list>
		</mu-bottom-sheet>
	</div>
</template>

<script>
	import Qs from "qs"
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				articleid:this.$route.query.actile,
				data: {},
				openSimple: false,
				open: false,
				img_url: '',
			}
		},
		methods: {
			closeSimpleDialog () {
		      this.openSimple = false;
		      this.closeBottomSheet()
		    },
			openSimpleDialog (data) {
				let that = this
				that.img_url = data.article_pic;
				that.openSimple = true;
		    },
		    closeBottomSheet () {
		      this.open = false;
		    },
		    openBotttomSheet () {
		      this.open = true;
		    },
			back() {
				window.history.back();
			},
			info_article() {
				var that = this;
				var params = {
					articleid: that.articleid
				};
				that.$http.post('/article/info_article', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
					}
				})
			},
			//			保存图片
			save(img_url){
				var that = this
				dsBridge.call('saveImg',that.baseUrl+that.img_url);
			}
		},
		created() {
			// 页面刚加载的时候执行
			this.info_article();

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.article-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.article-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.article-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.article-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.article-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.article-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
		padding-left: 1.7rem;
		padding-right: 1.7rem;
	}
	
	.article-shang {
		font-size: 1.7rem;
		color: #000000;
		padding-top: 1rem;
		font-weight: bold;
	}
	
	.article-flex {
		display: flex;
		padding-top: 1.5rem;
	}
	
	.article-shi img {
		width: auto;
		height: 1.2rem;
		margin-right: 0.5rem;
	}
	
	.article-jian {
		font-size: 1.2rem;
	}
	
	.article-zizhong {
		font-size: 1.4rem;
		color: #000000;
		padding-top: 1rem;
	}
	
	.article-tu img {
		width: 100%;
		height: auto;
		border-radius: 15px;
		/*padding: 1rem 3rem 1.5rem 3rem;*/
	}
	
	.article-xia {
		font-size: 1.4rem;
		color: #000000;
		padding-top: 1rem;
		padding-bottom: 2rem;
	}
	.article-tantu{
		margin-bottom: -1rem;
	}
	.article-tantu img{
		width: 100%;
		height: auto;
		margin: 0 auto;
	}
	.article-between{
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.article-flex{
		display: flex;
	}
	.article-xiazai img{
		height: 2rem;
		width: auto;
		margin-top: 1rem;
		margin-right: 1rem;
	}
	.article-baocun{
		font-size: 1.5rem;
		color: #000;
		/*text-align: center;*/
	}
	.article-qux{
		font-size: 1.5rem;
		padding-right: 1.5rem;
		color: #000000;
		padding-top: 1rem;
	}
	.article-lun{
		width: 100%;
		height: 100%;
		position: relative;
	}
	.article-lun img {
		width: 100%;
		/*height: 100%;*/
		height: auto;
		margin: 0 auto;
	}
	/deep/ .mu-dialog-body{
		padding: 0;
	}
	/deep/ .mu-dialog{
		max-width : 100%;
		background: #000000;
		-webkit-box-shadow: none;
		box-shadow: none;
	}
	
	/deep/ .mu-carousel {
		width: 100%;
		height: 75rem;
	}
	/deep/ .mu-tab {
		min-width: 0;
	}
	
	/deep/ .mu-carousel-indicator-icon {
		background: #fe4563;
	}
	
	/deep/ .mu-carousel-button.mu-icon-button {
		font-size: 0;
		z-index: 0;
	}
</style>


